import { Gasto } from "./Gasto";
import { useNavigate, useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import * as gastoServise from "./gastosService";
import { string } from "prop-types";

interface Props {
  gasto: Gasto;
}
const GastoDetail = () => {
    const navigate = useNavigate();
  const params = useParams();
  const initialState = {
    nombre: "",
    cantidad_mn_frank: 0,
    cantidad_usd_fabian: 0,
    cantidad_mn_fabian: 0,
    cantidad_usd_frank: 0,
    descripcion: "",
    user:"",
  };

  const [gasto, setGasto] = useState<Gasto>(initialState);
  const getGasto = async (id: string) => {
    const res = await gastoServise.getGasto(id);
    const {
        _id,
      nombre,
      cantidad_mn_fabian,
      cantidad_mn_frank,
      cantidad_usd_frank,
      cantidad_usd_fabian,
      descripcion,
      createdAt,
      user
    } = res.data;
    setGasto({
        _id,
      nombre,
      cantidad_mn_fabian,
      cantidad_mn_frank,
      cantidad_usd_frank,
      cantidad_usd_fabian,
      descripcion,
      createdAt,
      user
    });
  };
  useEffect(() => {
    if (params.id) getGasto(params.id);
  }, []);
  
  const fecha = gasto.createdAt ? new Date(gasto.createdAt) : new Date();
  return (
    <div>
      <div className="row">
        <div className="col-md-4 offset-md-4">
          <div className="card">
            <div className="card-body">
              <h4
              onDoubleClick={() => {
                navigate(`/update-gasto/${gasto._id}`);
              }}
              style={{textAlign:"center"}}
              >
                {gasto.nombre}
              </h4>
              <div className="row">
                
                <div className="col">
                  <p>Frank : {gasto.cantidad_usd_frank}</p>
                  <p>Fabian : {gasto.cantidad_usd_fabian}</p>
                  <p style={{fontSize:'1.1em'}}>Total  : {gasto.cantidad_usd_fabian + gasto.cantidad_usd_frank}</p>
                  <p>User : {gasto.user}</p>
                  <p>Fecha : {fecha.toLocaleString()}</p>
                  
                </div>
              </div>
              <p>{gasto.descripcion}</p>
              

              <button className="btn btn-danger" onClick={()=>navigate('/')}>Salir</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default GastoDetail;
