import React, { ChangeEvent, useEffect, useState } from "react";
import { Gasto, Totals } from "./Gasto";
import * as gastoService from "./gastosService";
import GastoItem from "./GastoItem";
import { BrowserView, MobileView } from "react-device-detect";
import GastoItemCel from "../Gastos/GastoItemCel";
import { Link } from "react-router-dom";

const GastosList = () => {
  const inicialStateTotals = {
    total_mn_frank: 0,
    total_usd_frank: 0,
    total_mn_fabian: 0,
    total_usd_fabian: 0,
  };
  const [gastos, setGastos] = useState<Gasto[]>([]);
  const [totals, setTotals] = useState<Totals>(inicialStateTotals);
  const [currentPage, setCurrentPage] = useState(0);
  const [search, setSearch] = useState("");
  const [itemsCount] = useState(7);

  const loadTotals = async () => {
    const res = await gastoService.getTotals();
    setTotals(res.data);
  };

  const loadGastos = async () => {
    const res = await gastoService.getGatos();
    const formated = res.data
      .map((gasto) => {
        return {
          ...gasto,
          createdAt: gasto.createdAt ? new Date(gasto.createdAt) : new Date(),
          updatedAt: gasto.updatedAt ? new Date(gasto.updatedAt) : new Date(),
        };
      })
      .sort((a, b) => b.createdAt.getTime() - a.createdAt.getTime());

    setGastos(formated);
  };
  const filterGastos = (): Gasto[] => {
    if (search.length === 0)
      return gastos.slice(currentPage, currentPage + itemsCount);
    const filtered = gastos.filter((gasto) => gasto.nombre.includes(search));
    const res = filtered.slice(currentPage, currentPage + itemsCount);
    //setCurrentPage(0);
    return res;
  };
  const getTotal = (): Number[] => {
    let cont1 = 0;
    let cont2 = 0;
    let cont3 = 0;
    let cont4 = 0;
    filterGastos().map((gasto) => {
      cont1 += gasto.cantidad_mn_frank;
      cont2 += gasto.cantidad_usd_frank;
      cont3 += gasto.cantidad_mn_fabian;
      cont4 += gasto.cantidad_usd_fabian;
    });
    return [cont1, cont2, cont3, cont4, cont1 + cont3, cont2 + cont4];
  };
  const nextPage = () => {
    if (
      gastos.filter((gasto) => gasto.nombre.includes(search)).length >
      currentPage + itemsCount
    ) {
      setCurrentPage(currentPage + itemsCount);
    }
  };
  const prevPage = () => {
    if (currentPage > 0) {
      setCurrentPage(currentPage - itemsCount);
    }
  };
  const onSearchChange = ({ target }: ChangeEvent<HTMLInputElement>) => {
    setCurrentPage(0);
    setSearch(target.value);
  };
  useEffect(() => {
    loadGastos();
    loadTotals();
  }, []);

  return (
    <div>
      <BrowserView>
        <div className="row">
          <div className="col-3">
            <button className="btn btn-info" onClick={prevPage}>
              Anteriores
            </button>
            &nbsp;
            <button className="btn btn-info" onClick={nextPage}>
              Siguientes
            </button>
          </div>
          <div className="col-5">
            <input
              type="text"
              className="mb-2 form-control"
              placeholder="Buscar Gasto"
              value={search}
              onChange={onSearchChange}
            />
          </div>
          <div className="col-2">
            <Link className="btn btn-primary" to="/new-gasto">
              +
            </Link>
          </div>
        </div>
        <table className="table mt-2">
          <thead className="thead-dark">
            <tr>
              <th scope="col" style={{ width: "220px" }}>
                Nombre
              </th>

              <th scope="col" className="table-col-mony">
                Frank USD
              </th>

              <th scope="col" className="table-col-mony">
                Fabian USD
              </th>
              <th scope="col">Descripcion</th>
            </tr>
          </thead>
          <tbody>
            {filterGastos().map((gasto) => {
              return <GastoItem gasto={gasto} key={gasto._id} />;
            })}
            <tr style={{ color: "darkgreen" }}>
              <th>Subtotal</th>
              <th>{getTotal()[1].toFixed(2)}</th>
              <th>{getTotal()[3].toFixed(2)}</th>

              <th>Total {getTotal()[5].toFixed(2)}</th>
            </tr>
          </tbody>
          <tfoot className="thead-dark">
            <tr>
              <th>Total</th>
              <th>{totals?.total_usd_frank.toFixed(2)}</th>
              <th>{totals?.total_usd_fabian.toFixed(2)}</th>

              <th>
                Total{" "}
                {(totals.total_usd_frank + totals.total_usd_fabian).toFixed(2)}
              </th>
            </tr>
          </tfoot>
        </table>
      </BrowserView>
      <MobileView>
        <div className="row">
          <div className="col-2">
            <button className="btn btn-info" onClick={prevPage}>
              {"<"}
            </button>
          </div>
          <div className="col-6">
            <input
              type="text"
              className="mb-2 form-control"
              placeholder="Buscar Gasto"
              value={search}
              onChange={onSearchChange}
            />
          </div>
          <div className="col-2">
            <button className="btn btn-info" onClick={nextPage}>
              {">"}
            </button>
          </div>
          <div className="col-2">
            <Link className="btn btn-primary" to="/new-gasto">
              +
            </Link>
          </div>
        </div>
        <table className="table mt-2">
          <thead className="thead-dark">
            <tr>
              <th scope="col" style={{ width: "1320px" }}>
                Nombre
              </th>
              <th scope="col"> Frank</th>
              <th scope="col"> Fabian</th>
            </tr>
          </thead>
          <tbody>
            {filterGastos().map((gasto) => {
              return <GastoItemCel gasto={gasto} key={gasto._id} />;
            })}
          </tbody>
          <tfoot className="thead-dark">
            <tr>
              <th>Total</th>

              <th>{totals.total_usd_frank.toFixed(2)}</th>
              <th>{totals.total_usd_fabian.toFixed(2)}</th>
            </tr>
          </tfoot>
        </table>
      </MobileView>
    </div>
  );
};

export default GastosList;
