import React, {  useEffect, useState } from "react";
import * as gastoService from "./gastosService";
import { BrowserView, MobileView } from "react-device-detect";
import { Link } from "react-router-dom";
import { Update } from "./Update";
import UpdateItem from "./UpdateItem";
import UpdateItemCel from "./UpdateItemCel";

const UpdateList = () => {
  const [updates, setUpdates] = useState<Update[]>([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [itemsCount] = useState(10);
 
  const loadUpdates = async () => {
    const res = await gastoService.getUpdates();
    const formated = res.data
      .map((update) => {
        return {
          ...update,
          createdAt: update.createdAt ? new Date(update.createdAt) : new Date(),
          updatedAt: update.updatedAt ? new Date(update.updatedAt) : new Date(),
        };
      })
      .sort((a, b) => b.createdAt.getTime() - a.createdAt.getTime());
      
    setUpdates(formated);
  };
  const filterUpdates = (): Update[] => {
    return updates.slice(currentPage, currentPage + itemsCount);
  };

  const nextPage = () => {
    if (updates.length > currentPage + itemsCount) {
      setCurrentPage(currentPage + itemsCount);
    }
  };
  const prevPage = () => {
    if (currentPage > 0) {
      setCurrentPage(currentPage - itemsCount);
    }
  };

  useEffect(() => {
    loadUpdates();
  }, []);

  return (
    <div>
      <BrowserView>
        <div className="row">
          <div className="col-3">
            <button className="btn btn-info" onClick={prevPage}>
              Anteriores
            </button>
            &nbsp;
            <button className="btn btn-info" onClick={nextPage}>
              Siguientes
            </button>
          </div>
        </div>
        <table className="table mt-2">
          <thead className="thead-dark">
            <tr>
              <th scope="col" style={{ width: "220px" }}>
                Gasto
              </th>
              <th scope="col" className="table-col-mony">
                Frank old
              </th>
              <th scope="col" className="table-col-mony">
                Frank
              </th>
              <th scope="col" className="table-col-mony">
                Fabian old
              </th>
              <th scope="col" className="table-col-mony">
                Fabian
              </th>
              <th scope="col" className="table-col-mony">
                User
              </th>
              <th scope="col" className="table-col-mony">
                Fecha
              </th>
            </tr>
          </thead>
          <tbody>
            {filterUpdates().map((update) => {
              return <UpdateItem update={update} key={update._id} />;
            })}
          </tbody>
        </table>
      </BrowserView>
      <MobileView>
        <div className="row">
          <div className="col-2">
            <button className="btn btn-info" onClick={prevPage}>
              {"<"}
            </button>
          </div>

          <div className="col-2">
            <button className="btn btn-info" onClick={nextPage}>
              {">"}
            </button>
          </div>
          <div className="col-2">
            <Link className="btn btn-primary" to="/new-gasto">
              +
            </Link>
          </div>
        </div>
        <table className="table mt-2">
          <thead className="thead-dark">
            <tr>
              <th scope="col" style={{width:'20px'}}>Nombre</th>
              <th scope="col"> MN</th>
              <th scope="col"> USD</th>
            </tr>
          </thead>
          <tbody>
            {filterUpdates().map((update) => {
              return <UpdateItemCel update={update} key={update._id} />;
            })}
          </tbody>
        </table>
      </MobileView>
    </div>
  );
};

export default UpdateList;
