import axios from 'axios' 
import { Gasto } from './Gasto'
import config from '../../config'
import { Update } from './Update';

const API = config.API;
export const getGatos = async()=>{
    return await axios.get<Gasto[]>(`${API}/gastos`)
}

export const getUpdates = async()=>{
    return await axios.get<Update[]>(`${API}/updates`)
}

export const getTotals = async()=>{
    return await axios.get(`${API}/totals`)
}

export const getGasto = async(id:string)=>{
    return await axios.get<Gasto>(`${API}/gasto/${id}`)
}

export const deleteGasto = async(id:string)=>{
    return await axios.delete<Gasto>(`${API}/gasto/${id}`)
}

export const createGatos = async(gasto:Gasto)=>{
    return await axios.post(`${API}/gasto`, gasto)
}

export const updateGasto = async(id:string| undefined, gasto: Gasto)=>{
    return await axios.put<Gasto>(`${API}/gasto/${id}`, gasto)
}