import React from "react";
import { Gasto } from "./Gasto";
import "./GastoItem.css";
import { useNavigate } from "react-router-dom";

interface Props {
  gasto: Gasto;
}

const GastoItemCel = ({ gasto }: Props) => {
  const navigate = useNavigate();
  return (
    <tr
      className="current-gasto"
      style={{ cursor: "pointer" }}
      onClick={() => {
        navigate(`/detail-gasto/${gasto._id}`);
      }}      
    >
      <td>{gasto.nombre}</td>      
      <td>{gasto.cantidad_usd_frank}</td>
      <td>{gasto.cantidad_usd_fabian}</td>
      
      
    </tr>
  );
};

export default GastoItemCel;
