import React from "react";

import "./GastoItem.css";
import { useNavigate } from "react-router-dom";
import { Update } from "./Update";

interface Props {
  update: Update;
}

const UpdateItem = ({ update }: Props) => {
  const navigate = useNavigate();
  return (
    <tr
      className="current-gasto"
      style={{ cursor: "pointer" }}
      onClick={() => {
        navigate(`/detail-gasto/${update.gasto}`);
      }}
    >
      <td>{update.gasto}</td>
      <td>{update.frank_usd_old}</td>
      <td>{update.frank_usd}</td>
      <td>{update.fabian_usd_old}</td>
      <td>{update.fabian_usd}</td>
      <td>{update.user}</td>
      <td>{update.createdAt?.toLocaleString()}</td>
    </tr>
  );
};

export default UpdateItem;
