import React from "react";
import LoginButton from "./LoginButton ";

const Redirect = () => {
  return (
    <div className="row mt-3">
      <div className="col-md-4 offset-md-4">
        <div className="card">
          <div className="card-body">
            <h4>Advertencia</h4>
            <div className="row">
              <div className="col">
                <p>Para realizar esta accion debe identificarse</p>
              </div>
            </div>
            {<LoginButton />}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Redirect;
