import React, { ChangeEvent, FormEvent, useEffect, useState } from "react";
import { Gasto } from "./Gasto";
import * as gastoServise from "./gastosService";
import { toast } from "react-toastify";
import { useNavigate, useParams } from "react-router-dom";
import { useAuth0 } from "@auth0/auth0-react";
import config from "../../config";

type InputChange = ChangeEvent<HTMLInputElement | HTMLTextAreaElement>;

const GastoFrom = () => {
  const { user, isAuthenticated } = useAuth0();
  const user_email = isAuthenticated ? user?.email : "none";
  const navigate = useNavigate();
  const params = useParams();
  const initialState = {
    nombre: "",
    cantidad_mn_frank: 0,
    cantidad_usd_fabian: 0,
    cantidad_mn_fabian: 0,
    cantidad_usd_frank: 0,
    descripcion: "",
    user:"",
  };


  const [gasto, setGasto] = useState<Gasto>(initialState);
  

  const handleInputChange = (e: InputChange) => {
    setGasto({ ...gasto, [e.target.name]: e.target.value,user:user_email });
  };
  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!params.id) {
      await gastoServise.createGatos(gasto);
      toast.success("Gasto guardado");
    } else {
      await gastoServise.updateGasto(params.id, gasto);
    }
    navigate("/");
  };

  const handleDelete = async (id: string) => {
    
    if (window.confirm('Es seguro de queres eliminar Gasto')) {
      await gastoServise.deleteGasto(id);
    }
  };

  const getGasto = async (id: string) => {
    const res = await gastoServise.getGasto(id);
    const {
      nombre,
      cantidad_mn_fabian,
      cantidad_mn_frank,
      cantidad_usd_frank,
      cantidad_usd_fabian,
      descripcion,
      user,
    } = res.data;
    setGasto({
      nombre,
      cantidad_mn_fabian,
      cantidad_mn_frank,
      cantidad_usd_frank,
      cantidad_usd_fabian,
      descripcion,
      user,
      
    });
  };

  useEffect(() => {
    if (params.id) getGasto(params.id);
  }, []);
  const email:any = user?.email;
  if (config.EMAILS.indexOf(email) === -1) {
    return(
      <div>
        <h1>{user?.name} no tiene autorización para realizar esta acción.</h1>
      </div>
    )
  }
  return (
    <div className="row">
      <div className="col-md-4 offset-md-4">
        <div className="card">
          <div className="card-body">
            <h3
              onDoubleClick={() => {
                params.id && handleDelete(params.id);
                navigate("/");
              }}
            >
              Nuevo Gasto
            </h3>
            <form onSubmit={handleSubmit}>
            <label htmlFor="">Nombre Gasto</label>
              <div className="form-group">
                <input
                  type="text"
                  name="nombre"
                  placeholder="Nombre del Gasto"
                  className="form-control"
                  autoFocus
                  value={gasto.nombre}
                  onChange={handleInputChange}
                />
              </div>
              
              <div className="form-group">
              <label htmlFor="">Frank USD</label>
                <input
                  type="number"
                  name="cantidad_usd_frank"
                  placeholder="Frank USD"
                  className="form-control"
                  onChange={handleInputChange}
                  value={gasto.cantidad_usd_frank}
                />
              </div>
              
              <div className="form-group">
                <label htmlFor="">Fabian USD</label>
                <input
                  type="number"
                  name="cantidad_usd_fabian"
                  placeholder="Fabian USD"
                  className="form-control"
                  onChange={handleInputChange}
                  value={gasto.cantidad_usd_fabian}
                />
              </div>
              <div className="form-group">
              <label htmlFor="">Descripcion</label>
                <textarea
                  name="descripcion"
                  id=""
                  rows={3}
                  className="form-control"
                  placeholder="Descripcion del Gasto"
                  onChange={handleInputChange}
                  value={gasto.descripcion}
                ></textarea>
                <div className="row py-4" >
                  <div className="col-6">
                    {params.id ? (
                      <button className="btn btn-info my-btn">Update</button>
                    ) : (
                      <button className="btn btn-primary my-btn">
                        Guardar
                      </button>
                    )}
                  </div>
                  <div className="col-6">
                    <button
                      style={{ width: "100px" }}
                      className="btn btn-danger"
                      onClick={() => navigate("/")}
                    >
                      Salir
                    </button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default GastoFrom;
