import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { ToastContainer } from "react-toastify";

//import "bootswatch/dist/cosmo/bootstrap.min.css";
import "./index.css";
import "react-toastify/dist/ReactToastify.css";

import GastosList from "./components/Gastos/GastosList";
import Navbar from "./components/Navbar/Navbar";
import GastoFrom from "./components/Gastos/GastoFrom";
import GastoDetail from "./components/Gastos/GastoDetail";
import { Auth0Provider, useAuth0 } from "@auth0/auth0-react";
import Redirect from "./components/Auth0/Redirect";
import UpdateList from "./components/Gastos/UpdateList";

const App = ()=>{
  const {isAuthenticated, isLoading} = useAuth0();
  if (isLoading) return <h1>Cargando...</h1>
  return(
    <BrowserRouter>
      <Navbar />
      <div className="container p-4" style={{ marginTop: "40px" }}>
        <Routes>
        <Route path="/" element={isAuthenticated ? <GastosList />: <Redirect />} />
          <Route path="/new-gasto" element={isAuthenticated ? <GastoFrom />: <Redirect />} />
          <Route path="/update-gasto/:id" element={isAuthenticated ? <GastoFrom />: <Redirect />} />
          <Route path="/detail-gasto/:id" element={isAuthenticated ? <GastoDetail />: <Redirect />} />
          <Route path="/updates" element={isAuthenticated ? <UpdateList />: <Redirect />} />          
        </Routes>
        <ToastContainer />
      </div>
    </BrowserRouter>
  )
}
ReactDOM.render(
  
  <React.StrictMode>
    <Auth0Provider
    domain="minegocio2022.us.auth0.com"
    clientId="12if1eDdtPWwIi2S6BeUgdjhBYUDdSmz"
    redirectUri={window.location.origin}
  >
  <App/>
  </Auth0Provider>
    
  </React.StrictMode>,
  document.getElementById("root")
);



